.widget {
  position: fixed;
  bottom: 15px;
  right: 15px;
  margin: 0 auto;
  padding: 20px 0;
  width: 200px;
  height: 200px;
  text-align: center;
  background-color: #fdfdfd;
  color: #000000;
  border-radius: 10px;
}

.close {
  position: absolute;
  top: 5px;
  right: 10px;
  font-size: 20px;
  cursor: pointer;
}

.link {
  margin-bottom: 10px;
  text-decoration: none;
  border: 1px solid black;
  color: black;
}

.widget__logo {
  margin-top: 20px;
  width: 100px;
  height: 50px;
}
